.note{
    background-color: #fef68a;
    border-radius: 10px;
    padding: 1rem;
    min-height: 170px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    white-space: pre-wrap;

}
body{
    margin: 0;
}
.container{
    max-width: 960px;
    margin-right: auto;
    margin-left: auto;
    padding-left: 50px;
    padding-right: 50px;
    min-height: 100vh;
}
.note-footer{
    display: flex;
    align-items: center;
    justify-content: space-between;

}
.notes-list{
    display: grid;
    grid-gap: 1rem;
    grid-template-columns: repeat(auto-fit,minmax(250px,1fr));
}
.note.new{
    background-color: #67d7cc;
}
textarea{
    border: none;
    resize: none;
    background-color: #67d7cc;
}
textarea:focus {
    outline: none;
}
.save{
    background-color: #e1e1e1;
    border: none;
    border-radius: 15px;
    padding: 5px 10px 5px 10px;
}
.save:hover{
    background-color: #ededed;
    cursor: pointer;
}
.delete-icon{
    cursor: pointer;
}
.search{
    display: flex;
    align-items: center;
    background-color: rgb(233, 233, 233);
    border-radius: 10px;
    padding: 5px;
    margin-bottom: 1.5rem;
}
.search input{
    background-color: rgb(233, 233, 233);
    border: none;
}
.search input:focus{
    outline: none;
}
.header{
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.darkmode{
    background-color: black;
}
.darkmode h1{
    color: white;
}
